export const Task = (name,description,dueDate,priority,done) => {
    return {
        set name (name) {return name},
        set description (description) {return description},
        set dueDate (dueDate) {
            if (dueDate === true || dueDate === false) return dueDate
        },
        set priority (priority) {
            if (priority === true || priority === false) return priority
        },
        set done (done) {return done},
        get name () {return name},
        get description () {return description},
        get dueDate () {return dueDate},
        get priority () {return priority},
        get done () {return done},
    }
}

export const Project = (id) => {
    const title = "New project";
    const tasks = [];
    const add = (task) => {
        tasks.push(task);
    }
    const remove = (task) => {
        let i = tasks.indexOf(task);
        if (i && i > 0) {
            tasks.splice(i,1);
        }
    }
    return {
        get tasks () {return tasks},
        get title () {return title},
        get id () {return id},
        set title (title) {return title},
        remove,
        add,
    };
}